interface IInfiniteGridOptions {
	itemSelector: string;
	isOverflowScroll: boolean;
	threshold: number;
	isEqualSize: boolean;
	isConstantSize: boolean;
	useRecycle: boolean;
	horizontal: boolean;
	transitionDuration: number;
	useFit: boolean;
	attributePrefix: string;
}
interface IInfiniteGridStatus {
	_status: {
		processingStatus: number,
		loadingSize: number,
		loadingStyle: StyleType,
	};
	_items: IItemManagerStatus;
	_renderer: IDOMRendererStatus;
	_watcher: IWatchStatus;
	_infinite: IInfiniteStatus;
}
/**
 * A module used to arrange card elements including content infinitely according to layout type. With this module, you can implement various layouts composed of different card elements whose sizes vary. It guarantees performance by maintaining the number of DOMs the module is handling under any circumstance
 * @ko          .         .        DOM      
 * @alias eg.InfiniteGrid
 * @extends eg.Component
 *
 * @example
```
<ul id="grid">
  <li class="card">
    <div>test1</div>
  </li>
  <li class="card">
    <div>test2</div>
  </li>
  <li class="card">
    <div>test3</div>
  </li>
  <li class="card">
    <div>test4</div>
  </li>
  <li class="card">
    <div>test5</div>
  </li>
  <li class="card">
    <div>test6</div>
  </li>
</ul>
<script>
var some = new eg.InfiniteGrid("#grid").on("layoutComplete", function(e) {
  // ...
});

// If you already have items in the container, call "layout" method.
some.layout();
</script>
```
 *
 * @support {"ie": "8+", "ch" : "latest", "ff" : "latest",  "sf" : "latest", "edge" : "latest", "ios" : "7+", "an" : "2.1+ (except 3.x)"}
 **/
class InfiniteGrid extends Component {
	/**
	 * Version info string
	 * @ko  
	 * @name VERSION
	 * @static
	 * @type {String}
	 * @example
	 * eg.InfiniteGrid.VERSION;  // ex) 3.3.3
	 * @memberof eg.InfiniteGrid
	 */
	public static VERSION = "#__VERSION__#";

	public options: IInfiniteGridOptions;
	private _loadingBar: {
		append?: HTMLElement,
		prepend?: HTMLElement,
	};
	private _items: ItemManager;
	private _renderer: DOMRenderer;
	private _manager: LayoutMananger;
	private _watcher: Watcher;
	private _infinite: Infinite;
	private _status: IInfiniteGridStatus["_status"];

	/**
	 * @param {HTMLElement|String|jQuery} element A base element for a module <ko>   </ko>
	 * @param {Object} [options] The option object of the eg.InfiniteGrid module <ko>eg.InfiniteGrid   </ko>
	 * @param {String} [options.itemSelector] A selector to select card elements that make up the layout<ko>     (selector)</ko>
	 * @param {Boolean} [options.useRecycle=true] Indicates whether keep the number of DOMs is maintained. If the useRecycle value is 'true', keep the number of DOMs is maintained. If the useRecycle value is 'false', the number of DOMs will increase as card elements are added. <ko>DOM    . useRecycle  'true' DOM   . useRecycle  'false'     DOM   .</ko>
	 * @param {Boolean} [options.isOverflowScroll=false] Indicates whether overflow:scroll is applied<ko>overflow:scroll  .</ko>
	 * @param {Boolean} [options.horizontal=false] Direction of the scroll movement (true: horizontal, false: vertical) <ko>   (true , false )</ko>
	 * @param {Boolean} [options.useFit=true] The useFit option scrolls upwards so that no space is visible until an item is added <ko>          .</ko>
	 * @param {Boolean} [options.isEqualSize=false] Indicates whether sizes of all card elements are equal to one another. If sizes of card elements to be arranged are all equal and this option is set to "true", the performance of layout arrangement can be improved. <ko>    .          'true'       </ko>
	 * @param {Boolean} [options.isConstantSize=false] Indicates whether sizes of all card elements does not change, the performance of layout arrangement can be improved. <ko>        'true'       </ko>
	 * @param {Number} [options.transitionDruation=0] Indicates how many seconds a transition effect takes to complete. <ko>     .</ko>
	 * @param {Number} [options.threshold=100] The threshold size of an event area where card elements are added to a layout.<ko>        .</ko>
	 * @param {String} [options.attributePrefix="data-"] The prefix to use element's data attribute.<ko>    .</ko>
	 */
	constructor(element: HTMLElement | string | IJQuery, options?: Partial<IInfiniteGridOptions>) {
		super();
		assign(this.options = {
			itemSelector: "*",
			isOverflowScroll: false,
			threshold: 100,
			isEqualSize: false,
			isConstantSize: false,
			useRecycle: true,
			horizontal: false,
			transitionDuration: 0,
			useFit: true,
			attributePrefix: "data-",
		}, options);
		DEFENSE_BROWSER && (this.options.useFit = false);
		IS_ANDROID2 && (this.options.isOverflowScroll = false);
		this._reset();
		this._loadingBar = {};

		const {
			isOverflowScroll,
			isEqualSize,
			isConstantSize,
			horizontal,
			threshold,
			useRecycle,
		} = this.options;

		this._items = new ItemManager();
		this._renderer = new DOMRenderer(element, {
			isEqualSize,
			isConstantSize,
			horizontal,
			container: isOverflowScroll,
		});
		this._watcher = new Watcher(
			this._renderer.view,
			{
				isOverflowScroll,
				horizontal,
				container: this._renderer.container,
				resize: () => this._onResize(),
				check: param => this._onCheck(param),
			});

		this._infinite = new Infinite(this._items, {
			useRecycle,
			threshold,
			append: param => this._requestAppend(param),
			prepend: param => this._requestPrepend(param),
			recycle: param => this._recycle(param),
		});
	}
	/**
	 * Adds a card element at the bottom of a layout. This method is available only if the isProcessing() method returns false.
	 * @ko     . isProcessing()   'false'      
	 *   isProcessing()  false   .
	 * @param {Array|jQuery} elements Array of the card elements to be added <ko>   </ko>
	 * @param {Number|String} [groupKey] The group key to be configured in a card element. It is automatically generated by default.
	 * <ko>     .    .</ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 * @example
	 * infinitegrid.append("&lt;div class='item'&gt;test1&lt;/div&gt;&lt;div class='item'&gt;test2&lt;/div&gt;");
	 * infinitegrid.append(["&lt;div class='item'&gt;test1&lt;/div&gt;", "&lt;div class='item'&gt;test2&lt;/div&gt;"]);
	 * infinitegrid.append([HTMLElement1, HTMLElement2]);
	 * infinitegrid.append(jQuery(["&lt;div class='item'&gt;test1&lt;/div&gt;", "&lt;div class='item'&gt;test2&lt;/div&gt;"]));
	 */
	public append(elements: HTMLElement[] | IJQuery | string[] | string, groupKey?: string | number) {
		this._manager && this._insert({
			elements,
			isAppend: APPEND,
			groupKey,
		});
		return this;
	}
	/**
	 * Adds a card element at the top of a layout. This method is available only if the isProcessing() method returns false.
	 * @ko     . isProcessing()   'false'      
	 * @param {Array|jQuery} elements Array of the card elements to be added <ko>   </ko>
	 * @param {Number|String} [groupKey] The group key to be configured in a card element. It is automatically generated by default.
	 * <ko>     .    .</ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 * @example
	 * infinitegrid.prepend("&lt;div class='item'&gt;test1&lt;/div&gt;&lt;div class='item'&gt;test2&lt;/div&gt;");
	 * infinitegrid.prepend(["&lt;div class='item'&gt;test1&lt;/div&gt;", "&lt;div class='item'&gt;test2&lt;/div&gt;"]);
	 * infinitegrid.prepend([HTMLElement1, HTMLElement2]);
	 * infinitegrid.prepend(jQuery(["&lt;div class='item'&gt;test1&lt;/div&gt;", "&lt;div class='item'&gt;test2&lt;/div&gt;"]));
	 */
	public prepend(elements: HTMLElement[] | IJQuery | string[] | string, groupKey?: string | number) {
		this._manager && this._insert({
			elements,
			isAppend: PREPEND,
			groupKey,
		});
		return this;
	}
	/**
	 * Specifies the Layout class to use.
	 * @ko  Layout  .
	 * @param {Class|Object} LayoutKlass The Layout class to use or an instance of a layout moudle<ko> Layout     </ko>
	 * @param {Object} options Options to apply to the Layout.<ko>Layout  </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 * @example
	 * infinitegrid.setLayout(eg.InfiniteGrid.GridLayout, {
	 *  margin: 10,
	 *  align: "start"
	 * });
	 * infinitegrid.setLayout(eg.InfiniteGrid.JustifiedLayout, {
	 *  margin: 10,
	 *  minSize: 100,
	 *  maxSize: 200
	 * });
	 * infinitegrid.setLayout(eg.InfiniteGrid.SquareLayout, {
	 *  margin: 10,
	 *  column: 2
	 * });
	 * infinitegrid.setLayout(eg.InfiniteGrid.FrameLayout, {
	 *  margin: 10,
	 *  frame: [
	 *   [1, 2],
	 *   [4, 3],
	 *  ]
	 * });
	 * infinitegrid.setLayout(eg.InfiniteGrid.PackingLayout, {
	 *  margin: 10,
	 *  aspectRatio: 1.5
	 * });
	 * var layout = new eg.InfiniteGrid.GridLayout({
	 *   margin: 10,
	 *  align: "start"
	 * });
	 * infinitegrid.setLayout(layout);
	 */
	public setLayout(LayoutKlass: ILayout | (new (...args: any[]) => ILayout), options = {}) {
		const { isEqualSize, isConstantSize, attributePrefix, horizontal } = this.options;

		if (!this._manager) {
			this._manager = new LayoutMananger(this._items, this._renderer, {
				attributePrefix,
				isEqualSize,
				isConstantSize,
			});
		}
		if (typeof LayoutKlass === "function") {
			this._manager.setLayout(new LayoutKlass(assign(options, {
				horizontal,
			})));
		} else {
			LayoutKlass.options.horizontal = horizontal;
			this._manager.setLayout(LayoutKlass);
		}
		this._renderer.resize();
		this._setSize(this._renderer.getViewportSize());
		return this;
	}
	/**
	 * Returns the layouted items.
	 * @ko   .
	 * @param {Boolean} includeCached Indicates whether to include the cached items. <ko>    .</ko>
	 * @returns List of items <ko> </ko>
	 */
	public getItems(includeCached = false): IInfiniteGridItem[] {
		return includeCached ? this._items.pluck("items") : this._infinite.getVisibleItems();
	}
	/**
	 * Rearranges a layout.
	 * @ko   .
	 * @param {Boolean} [isRelayout=true] Indicates whether a card element is being relayouted <ko>   </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public layout(isRelayout = true) {
		if (!this._manager) {
			return this;
		}
		const renderer = this._renderer;
		const itemManager = this._items;
		const infinite = this._infinite;
		const isResize = renderer.resize();
		const items = this.getItems();
		const { isEqualSize, isConstantSize, transitionDuration } = this.options;
		const isLayoutAll = isRelayout && (isEqualSize || isConstantSize);
		const size = itemManager.size();

		if (isRelayout) {
			this._watcher.resize();
			if (isResize) {
				this._setSize(renderer.getViewportSize());
			}
		}
		// check childElement
		if (!size || !items.length) {
			const children = toArray(renderer.container.children);

			if (children.length) {
				this._insert({
					elements: children,
					isAppend: true,
					isChildren: true,
				});
			} else {
				if (renderer.getContainerSize()) {
					renderer.setContainerSize(0);
				}
				this._requestAppend({});
			}
			return this;
		}
		// layout datas
		const startCursor = infinite.getCursor("start");
		const endCursor = infinite.getCursor("end");
		const data = isLayoutAll || !(isRelayout && isResize) ? itemManager.get() :
			itemManager.get(startCursor, endCursor);

		// LayoutManger interface
		this._manager.layout(isRelayout, data, isResize ? items : []);
		if (isLayoutAll) {
			this._fit();
		} else if (isRelayout && isResize) {
			itemManager.clearOutlines(startCursor, endCursor);
		}
		DOMRenderer.renderItems(items, transitionDuration);
		isRelayout && this._watcher.setScrollPos();
		this._onLayoutComplete({
			items,
			isAppend: APPEND,
			fromCache: CACHE,
			isTrusted: NO_TRUSTED,
			useRecycle: false,
			isLayout: true,
		});
		return this;
	}
	/**
	 * Removes a item element on a grid layout.
	 * @ko     .
	 * @param {HTMLElement} item element to be removed <ko>  </ko>
	 * @return {Object}  Removed item element <ko>   </ko>
	 */
	public remove(element: HTMLElement, isLayout = true) {
		if (element) {
			const {items, groups} = this._infinite.remove(element);

			items && DOMRenderer.removeElement(element);
			isLayout && this.layout(groups.length > 0);
			return items;
		}
		return null;
	}
	/**
	 * Returns the list of group keys which belongs to card elements currently being maintained. You can use the append() or prepend() method to configure group keys so that multiple card elements can be managed at once. If you do not use these methods to configure group keys, groupkey is automatically generated.
	 * @ko         .         append()  prepend()      . append()  prepend()        .
	 * @param {Boolean} includeCached Indicates whether to include the cached groups. <ko>    .</ko>
	 * @return {Array} List of group keys <ko>  </ko>
	 */
	public getGroupKeys(includeCached?: boolean) {
		const data = includeCached ?
			this._items.get() : this._infinite.getVisibleData();

		return data.map(v => v.groupKey);
	}
	/**
	 * Returns the current state of a module such as location information. You can use the setStatus() method to restore the information returned through a call to this method.
	 * @ko         .       setStatus()    
	 * @return {Object} State object of the eg.InfiniteGrid module<ko>eg.InfiniteGrid   </ko>
	 */
	public getStatus(startKey?: string | number, endKey?: string | number): IInfiniteGridStatus {
		return {
			_status: assign({}, this._status),
			_items: this._items.getStatus(startKey, endKey),
			_renderer: this._renderer.getStatus(),
			_watcher: this._watcher.getStatus(),
			_infinite: this._infinite.getStatus(startKey, endKey),
		};
	}
	/**
	 * Sets the state of the eg.InfiniteGrid module with the information returned through a call to the getStatue() method.
	 * @ko getStatue()    eg.InfiniteGrid   .
	 * @param {Object} status State object of the eg.InfiniteGrid module <ko>eg.InfiniteGrid   </ko>
	 * @param {boolean} [applyScrollPos=true] Checks whether to scroll<ko>   .</ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public setStatus(status: IInfiniteGridStatus, applyScrollPos = true) {
		if (!status) {
			return this;
		}
		const { _status, _renderer, _items, _watcher, _infinite } = status;

		if (!_status ||
			!_renderer || !_items || !_watcher || !_infinite) {
			return this;
		}
		const items = this._items;
		const renderer = this._renderer;
		const watcher = this._watcher;
		const infinite = this._infinite;

		watcher.detachEvent();
		assign(this._status, _status);
		this._status.processingStatus = IDLE;
		items.setStatus(_items);
		renderer.setStatus(_renderer);
		infinite.setStatus(_infinite);

		const visibleItems = this.getItems();
		const length = visibleItems.length;

		renderer.createAndInsert(visibleItems, true);

		const isReLayout = renderer.isNeededResize();

		watcher.setStatus(_watcher, applyScrollPos);
		watcher.attachEvent();

		const { isConstantSize, isEqualSize } = this.options;

		if (!length) {
			this._requestAppend({ cache: [] });
		} else if (isReLayout) {
			renderer.resize();
			this._setSize(renderer.getViewportSize());

			if (isConstantSize) {
				this.layout(true);
			} else {
				this._items.clearOutlines();
				this._postLayout({
					fromCache: true,
					groups: isEqualSize ? items.get() : infinite.getVisibleData(),
					items: visibleItems,
					newItems: visibleItems,
					isAppend: true,
					isTrusted: false,
				});
			}
		} else {
			this.layout(false);
		}
		return this;
	}
	/**
	 * Clears added card elements and data.
	 * @ko      .
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public clear() {
		this._items.clear();
		this._renderer.clear();
		this._infinite.clear();
		this._reset();
		this._appendLoadingBar();
		return this;
	}
	/**
	 * Specifies the Loading Bar to use for append or prepend items.
	 * @ko  append  prepend      .
	 * @param {String|Object} [userLoadingBar={}] The loading bar HTML markup or element or element selector <ko>   HTML  element  selector </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public setLoadingBar(userLoadingBar: {
		append?: string | HTMLElement,
		prepepnd?: string | HTMLElement,
	} | string = {}) {
		const loadingBarObj: {
			append?: string | HTMLElement,
			prepend?: string | HTMLElement,
		} = typeof userLoadingBar === "object" ? userLoadingBar : {
			append: userLoadingBar,
			prepend: userLoadingBar,
		};

		this._status.loadingSize = 0;
		this._status.loadingStyle = {};
		const loadingBar = this._loadingBar;

		for (const type in loadingBarObj) {
			loadingBar[type as "append" | "prepend"] = $(loadingBarObj[type as "append" | "prepend"]);
			loadingBar[type as "append" | "prepend"].className += ` ${IGNORE_CLASSNAME}`;
		}
		this._appendLoadingBar();
		return this;
	}
	/**
	 * Checks whether a card element or data is being added.
	 * @ko         
	 * @return {Boolean} Indicates whether a card element or data is being added <ko>        </ko>
	 */
	public isProcessing() {
		return this._isProcessing() || this._isLoading();
	}
	/**
	 * Returns the element of loading bar.
	 * @ko   element .
	 * @param {Boolean} [isAppend=currentLoadingBar|true] Checks whether the card element is added to the append () method. <ko>  append()     .</ko>
	 * @return {Element} The element of loading bar. <ko>  element</ko>
	 */
	public getLoadingBar(isAppend = this._getLoadingStatus() !== LOADING_PREPEND) {
		return this._loadingBar[isAppend ? "append" : "prepend"];
	}
	/**
	 * Start loading for append/prepend during loading data.
	 * @ko    append/prepend   .
	 * @param {Boolean} [isAppend=true] Checks whether the card element is added to the append () method. <ko>  append()     .</ko>
	 * @param {Object} [userStyle = {display: "block"}] custom style to apply to this loading bar for start. <ko>         </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public startLoading(isAppend?: boolean, userStyle: StyleType = { display: "block" }) {
		if (this._isLoading()) {
			return this;
		}
		const type = isAppend ? "append" : "prepend";

		this._process(isAppend ? LOADING_APPEND : LOADING_PREPEND);
		if (!this._loadingBar[type]) {
			return this;
		}
		this._renderLoading(userStyle);
		this._status.loadingStyle = userStyle;
		if (!isAppend) {
			this._fit();
		} else {
			this._setContainerSize(this._getEdgeValue("end") + this._status.loadingSize);
		}
		return this;
	}
	/**
	 * End loading after startLoading() for append/prepend
	 * @ko  append/prepend  startLoading()    .
	 * @param {Object} [userStyle = {display: "none"}] custom style to apply to this loading bar for end <ko>         </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public endLoading(userStyle: StyleType = { display: "none" }) {
		if (!this._isLoading()) {
			return this;
		}
		const isAppend = this._getLoadingStatus() === LOADING_APPEND;
		const type = isAppend ? "append" : "prepend";
		const el = this._loadingBar[type];
		const status = this._status;
		const size = status.loadingSize;

		this._process(LOADING_APPEND | LOADING_PREPEND, false);
		status.loadingSize = 0;
		status.loadingStyle = {};
		if (el) {
			const style: StyleType = assign({
				[this.options.horizontal ? "left" : "top"]: `${-size}px`,
			}, userStyle);

			for (const property in style) {
				el.style[property as keyof StyleType] = style[property as keyof StyleType];
			}
			if (!isAppend) {
				this._fitItems(size);
			} else {
				this._setContainerSize(this._getEdgeValue("end"));
			}
		}
		if (this.options.useRecycle && !this.isProcessing()) {
			this._infinite.recycle(this._watcher.getScrollPos(), isAppend);
		}
		return this;
	}
	/**
	 * Retrieves the item via index or the element.
	 * @ko index  element   .
	 * @param {number | HTMLElement} [groupIndex=0] The element corresponding to item or the index of the group where the item is in position <ko> item  element   item  group index</ko>
	 * @param {number} [itemIndex] If groupIndex is used, the index of the item in the group <ko> groupIndex    group  Item index </ko>
	 * @return The item containing the content, size and position,etc<ko>content, size, position   item </ko>
	 * @example

	 ig.getItem(0, 0);
	 ig.getItem(element);

	 {
	  el: HTMLElement,
	  content: "<div>...</div>",
	  size: {width: ..., height: ...},
	  rect: {top: ..., left: ..., width: ..., height: ...},
	 }
	 */
	public getItem(groupIndex = 0, itemIndex?: number): IInfiniteGridItem {
		if (itemIndex == null && typeof groupIndex === "object") {
			if (!groupIndex) {
				return undefined;
			}
			const items = this.getItems();
			const length = items.length;

			for (let i = 0; i < length; ++i) {
				if (items[i].el === groupIndex) {
					return items[i];
				}
			}
			return undefined;
		} else {
			const group = this._items.getData(groupIndex);

			return group && group.items[itemIndex || 0];
		}
	}
	/**
	 * Updates the item via index or the element.
	 * @ko index  element   .
	 * @param {number | HTMLElement} [groupIndex=0] The element corresponding to item or the index of the group where the item is in position <ko> item  element   item  group index</ko>
	 * @param {number} [itemIndex] If groupIndex is used, the index of the item in the group <ko> groupIndex    group  Item index </ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 * @example
	element.innerHTML = "2";
	element.style.height = "400px";
	ig.updateItem(element);
	ig.updateItem(0, 0);
	 */
	public updateItem(groupIndex?: number, itemIndex?: number) {
		const item = this.getItem(groupIndex, itemIndex);

		this._updateItem(item) && this.layout(false);

		return this;
	}
	/**
	 * Update the currently displayed items.
	 * @ko   .
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 * @example
	element.innerHTML = "2";
	element.style.height = "400px";

	element2.innerHTML = "2";
	element2.style.height = "400px";

	ig.updateItems();
	 */
	public updateItems() {
		this.getItems().forEach(item => {
			this._updateItem(item);
		});
		this.layout(false);
		return this;
	}
	/**
	 * Move to some group or item position.
	 * @ko      .
	 * @param {Number} [index] group's index <ko>  index</ko>
	 * @param {Number} [itemIndex=-1] item's index <ko>  index</ko>
	 * @return {eg.InfiniteGrid} An instance of a module itself<ko>  </ko>
	 */
	public moveTo(index?: number, itemIndex = 0) {
		if (this.isProcessing()) {
			return this;
		}
		const data = this._items.getData(index);

		if (!data) {
			return this;
		}
		const infinite = this._infinite;
		const outlines = data.outlines;
		const items = data.items;
		const item = items[itemIndex];
		const isResize = outlines.start && (outlines.start.length === 0);
		const startCursor = infinite.getCursor("start");
		const endCursor = infinite.getCursor("end");
		const isInCursor = startCursor <= index && index <= endCursor;
		const { useRecycle, horizontal } = this.options;

		if (isInCursor || !useRecycle || !isResize) {
			let pos = item ? item.rect[horizontal ? "left" : "top"] : Math.max(...outlines.start);
			const fit = Math.min(...outlines.start);

			if (fit < 0) {
				// base < 0
				this._fitItems(fit, 0);
				pos -= fit;
			}
			const isAppend = index > startCursor;

			if (isInCursor || isAppend) {
				this._scrollTo(pos);
				return this;
			}
			this._postLayout({
				fromCache: true,
				groups: [data],
				items,
				newItems: [],
				isAppend,
				isTrusted: false,
				moveCache: true,
				moveItem: itemIndex,
			});
			return this;
		} else {
			const isAppend = index > endCursor || index < startCursor - 1;

			this._postCache({
				isAppend,
				cache: [data],
				isTrusted: false,
				moveItem: itemIndex,
			});
		}
		return this;
	}
	/**
   * Destroys elements, properties, and events used on a grid layout.
   * @ko     ,  
   */
	public destroy() {
		this._infinite.clear();
		this._watcher.destroy();
		this._manager.destroy();
		this._reset();
		this._items.clear();
		this._renderer.destroy();
	}
	private _setContainerSize(size: number) {
		this._renderer.setContainerSize(Math.max(this._items.getMaxEdgeValue(), size));
	}
	private _appendLoadingBar() {
		const loadingBar = this._loadingBar;
		const container = this._renderer.container;

		for (const type in loadingBar) {
			container.appendChild(loadingBar[type as "append" | "prepend"]);
		}
	}
	private _setSize(size: number) {
		this._infinite.setSize(this._renderer.getViewSize());
		this._manager.setSize(size);
	}
	private _fitItems(base: number, margin = 0) {
		base > 0 && this._watcher.scrollBy(-base);
		this._items.fit(base, this.options.horizontal);
		DOMRenderer.renderItems(this.getItems());
		this._setContainerSize(this._getEdgeValue("end") || margin);
		base < 0 && this._watcher.scrollBy(-base);
	}
	// called by visible
	private _fit(useFit = this.options.useFit) {
		let base = this._getEdgeValue("start");
		const margin = (this._getLoadingStatus() === LOADING_PREPEND && this._status.loadingSize) || 0;
		const { isConstantSize, isEqualSize, useRecycle } = this.options;

		if (!useRecycle || !useFit || isConstantSize || isEqualSize) {
			if (base < margin) {
				this._fitItems(base - margin, margin);
			}
			base = 0;
		} else if (base !== 0 || margin) {
			this._fitItems(base - margin, margin);
		} else {
			return 0;
		}
		this._isLoading() && this._renderLoading();
		return base;
	}
	private _getEdgeValue(cursor: CursorType) {
		return this._infinite.getEdgeValue(cursor);
	}
	private _isProcessing() {
		return (this._status.processingStatus & PROCESSING) > 0;
	}
	private _isLoading() {
		return this._getLoadingStatus() > 0;
	}
	private _getLoadingStatus() {
		return this._status.processingStatus & (LOADING_APPEND | LOADING_PREPEND);
	}
	private _process(status: number, isAdd = true) {
		if (isAdd) {
			this._status.processingStatus |= status;
		} else {
			this._status.processingStatus -= this._status.processingStatus & status;
		}
	}
	private _insert({
		elements,
		isAppend,
		isChildren,
		groupKey = new Date().getTime() + Math.floor(Math.random() * 1000),
	}: {
		elements: string | string[] | HTMLElement[] | IJQuery,
		isAppend?: boolean,
		isChildren?: boolean,
		groupKey?: string | number,
	}) {
		if (this._isProcessing() || elements.length === 0) {
			return;
		}
		const items = ItemManager.from(elements, this.options.itemSelector, {
			groupKey,
		});

		if (!items.length) {
			return;
		}

		const group: IInfiniteGridGroup = {
			groupKey,
			items,
			outlines: { start: [], end: [] },
		};
		const method = isAppend ? "append" : "prepend";

		this._items[method](group);

		if (!isAppend) {
			const infinite = this._infinite;
			const startCursor = infinite.getCursor("start");
			const endCursor = infinite.getCursor("end");

			infinite.setCursor("start", startCursor + 1);
			infinite.setCursor("end", endCursor + 1);
		}
		this._postLayout({
			fromCache: NO_CACHE,
			groups: [group],
			items,
			newItems: items,
			isAppend,
			isChildren,
			isTrusted: NO_TRUSTED,
		});
	}
	// add items, and remove items for recycling
	private _recycle({ start, end }: { start: number, end: number }) {
		if (!this.options.useRecycle) {
			return;
		}
		DOMRenderer.removeItems(this._items.pluck("items", start, end));
	}
	private _renderLoading(userStyle = this._status.loadingStyle) {
		if (!this._isLoading()) {
			return;
		}
		const isAppend = this._getLoadingStatus() === LOADING_APPEND;
		const el = this._loadingBar[isAppend ? "append" : "prepend"];

		if (!el) {
			return;
		}
		const style = assign({
			position: "absolute",
		}, userStyle);

		for (const property in style) {
			el.style[property as keyof StyleType] = style[property as keyof StyleType];
		}
		this._status.loadingSize = this.options.horizontal ? outerWidth(el) : outerHeight(el);
		const posName = this.options.horizontal ? "left" : "top";

		if (!(posName in style)) {
			const pos = isAppend ? this._getEdgeValue("end") : this._getEdgeValue("start") - this._status.loadingSize;

			el.style[posName] = `${pos}px`;
		}
	}
	private _updateItem(item: IInfiniteGridItem) {
		if (item && item.el) {
			item.content = item.el.outerHTML;

			!this.options.isEqualSize && resetSize(item);
			this._renderer.updateSize([item]);
			return true;
		}
		return false;
	}
	private _setScrollPos(pos: number) {
		this._watcher.setScrollPos(this._watcher.getContainerOffset() + pos);
	}
	private _scrollTo(pos: number) {
		this._watcher.scrollTo(this._watcher.getContainerOffset() + pos);
	}
	private _onImageError(e: IErrorCallbackOptions) {
		/**
		 * This event is fired when an error occurs in the image.
		 * @ko       .
		 * @event eg.InfiniteGrid#imageError
		 * @param {eg.InfiniteGrid.IErrorCallbackOptions} e The object of data to be sent to an event <ko>   </ko>
		 * @example
	ig.on("imageError", e => {
	  e.remove();
	  e.removeItem();
	  e.replace("http://...jpg");
	  e.replace(imageElement);
	  e.replaceItem("item html");
	});
		 */
		this.trigger("imageError", assign(e, { element: e.item.el }));
	}
	private _postCache({
		cache,
		isAppend,
		isTrusted = true,
		moveItem = -1,
	}: {
		cache: IInfiniteGridGroup[],
		isAppend?: boolean,
		isTrusted?: boolean,
		moveItem?: number,
	}) {
		const { isConstantSize } = this.options;
		const items = ItemManager.pluck(cache, "items");
		let fromCache = true;
		const newItems = items.filter(item => {
			if (!item.orgSize) {
				fromCache = false;
				return true;
			}
			return !isConstantSize && item.rect.top < DUMMY_POSITION / 10;
		});

		this._postLayout({
			fromCache,
			groups: cache,
			items,
			newItems,
			isAppend,
			isTrusted,
			moveItem,
		});
	}
	private _postLayout({
		fromCache,
		groups,
		items = ItemManager.pluck(groups, "items"),
		newItems,
		isAppend,
		isChildren,
		isTrusted,
		moveCache,
		moveItem = -2,
	}: {
		fromCache: boolean,
		groups: IInfiniteGridGroup[],
		items: IInfiniteGridItem[],
		newItems?: IInfiniteGridItem[],
		isAppend: boolean,
		isChildren?: boolean,
		isTrusted?: boolean,
		moveCache?: boolean,
		moveItem?: number,
	}) {
		this._process(PROCESSING);
		const method = isAppend ? "append" : "prepend";
		const itemManager = this._items;
		const horizontal = this.options.horizontal;

		// If container has children, it does not render first.
		if (!isChildren) {
			DOMRenderer.createElements(items);
			this._renderer[method](items);
		}
		this._manager[method]({
			groups,
			items: newItems,
		}, {
				complete: () => {
					const infinite = this._infinite;
					const startCursor = Math.max(infinite.getCursor("start"), 0);
					const endCursor = Math.max(infinite.getCursor("end"), 0);
					let requestStartCursor = itemManager.indexOf(groups[0].groupKey);
					let requestEndCursor = itemManager.indexOf(groups[groups.length - 1].groupKey);
					let isInCursor = true;

					if (requestStartCursor > endCursor + 1 || requestEndCursor < startCursor - 1) {
						isInCursor = false;
					}
					if (isInCursor) {
						if (isAppend) {
							requestStartCursor = startCursor;
							requestEndCursor = Math.max(endCursor, requestEndCursor);
						} else {
							requestStartCursor = Math.max(Math.min(startCursor, requestStartCursor), 0);
							requestEndCursor = endCursor;
						}
					}

					!isInCursor && this._recycle({ start: startCursor, end: endCursor });
					infinite.setCursor("start", requestStartCursor);
					infinite.setCursor("end", requestEndCursor);

					if (moveItem > -1) {
						const pos = items[moveItem].rect[horizontal ? "left" : "top"];

						if (!isInCursor && !moveCache) {
							itemManager.clearOutlines(requestStartCursor, requestEndCursor);
						}
						this._scrollTo(pos);
						this._setScrollPos(pos);
					}
					this._onLayoutComplete({
						items,
						isAppend,
						fromCache,
						isTrusted,
						useRecycle: false,
					});
				},
				error: e => this._onImageError(e),
				end: ({ remove, layout }) => {
					remove.forEach(el => this.remove(el, false));
					if (layout) {
						this.layout(false);
					} else if (!this.isProcessing() && this.options.useRecycle) {
						const watcher = this._watcher;
						const scrollPos = watcher.getScrollPos();

						this._infinite.recycle(scrollPos, isAppend);
					}
				},
			});
		return this;
	}
	// called by visible
	private _requestAppend({ cache }: { cache?: IInfiniteGridGroup[] }) {
		if (this._isProcessing()) {
			return;
		}
		if (cache && cache.length) {
			this._postCache({ cache, isAppend: APPEND });
		} else {
			/**
			 * This event is fired when a card element must be added at the bottom or right of a layout because there is no card to be displayed on screen when a user scrolls near bottom or right.
			 * @ko          .          
			 * @event eg.InfiniteGrid#append
			 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
			 * @param {String|Number} groupKey The group key of the first group visible on the screen <ko>    </ko>
			 * @param {Boolean} param.isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
			 * @param {Function} param.startLoading Start loading for append loading data. <ko>     . </ko>
			 * @param {Object} param.startLoading.userStyle The custom style to apply to this loading bar for start. <ko>         </ko>
			 * @param {Function} param.endLoading End loading after startLoading() for append/prepend loading data. <ko>   append/prepend startLoading()    .</ko>
			 * @param {Object} param.endLoading.userStyle The custom style to apply to this loading bar for start. <ko>         </ko>
			 */
			this.trigger("append", {
				isTrusted: TRUSTED,
				groupKey: this.getGroupKeys().pop() || "",
				startLoading: (userStyle: StyleType) => {
					this.startLoading(true, userStyle);
				},
				endLoading: (userStyle: Partial<CSSStyleDeclaration>) => {
					this.endLoading(userStyle);
				},
			});
		}
	}
	// called by visible
	private _requestPrepend({ cache }: { cache?: IInfiniteGridGroup[] }) {
		this._fit(this.options.useFit || !cache.length);
		if (this._isProcessing()) {
			return;
		}
		if (cache && cache.length) {
			this._postCache({ cache, isAppend: PREPEND });
		} else {
			/**
			 * This event is fired when a card element must be added at the top or left of a layout because there is no card to be displayed on screen when a user scrolls near top or left.
			 * @ko         .          .
			 * @event eg.InfiniteGrid#prepend
			 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
			 * @param {String|Number} groupKey The group key of the first group visible on the screen <ko>    </ko>
			 * @param {Boolean} param.isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
			 * @param {Function} param.startLoading Start loading for prepend loading data. <ko>     . </ko>
			 * @param {Object} param.startLoading.userStyle The custom style to apply to this loading bar for start. <ko>         </ko>
			 * @param {Function} param.endLoading End loading after startLoading() for append/prepend loading data. <ko>   append/prepend startLoading()    .</ko>
			 * @param {Object} param.endLoading.userStyle The custom style to apply to this loading bar for start. <ko>         </ko>
			 */
			this.trigger("prepend", {
				isTrusted: TRUSTED,
				groupKey: this.getGroupKeys().shift(),
				startLoading: (userStyle: StyleType) => {
					this.startLoading(false, userStyle);
				},
				endLoading: (userStyle: StyleType) => {
					this.endLoading(userStyle);
				},
			});
		}
	}
	private _onResize() {
		this.layout(true);
	}
	private _onCheck({
		isForward,
		scrollPos,
		horizontal,
		orgScrollPos,
	}: {
		isForward: boolean,
		scrollPos: number,
		horizontal: boolean,
		orgScrollPos: number,
	}) {
		/**
		 * This event is fired when the user scrolls.
		 * @ko      .
		 * @event eg.InfiniteGrid#change
		 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
		 * @param {Boolean} param.isForward Indicates whether the scroll progression direction is forward or backword. <ko>    ,   .</ko>
		 * @param {Number} param.scrollPos Current scroll position value relative to the infiniteGrid container element. <ko>infiniteGrid      </ko>
		 * @param {Boolean} param.orgScrollPos Current position of the scroll <ko>  </ko>
		 * @param {Boolean} param.isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
		 * @param {Boolean} options.horizontal Direction of the scroll movement (true: horizontal, false: vertical) <ko>   (true , false </ko>
		 */
		this.trigger("change", {
			isForward,
			horizontal,
			scrollPos,
			orgScrollPos,
		});
		this._infinite.scroll(scrollPos);
	}
	private _onLayoutComplete({
		items,
		isAppend,
		isTrusted = false,
		useRecycle = this.options.useRecycle,
		fromCache = false,
		isLayout = false,
	}: {
		items: IInfiniteGridItem[],
		isAppend: boolean,
		isTrusted: boolean,
		useRecycle: boolean,
		fromCache: boolean,
		isLayout?: boolean,
	}) {
		const viewSize = this._renderer.getViewSize();

		if (!isAppend) {
			this._fit();
		} else {
			this._isLoading() && this._renderLoading();
		}

		const watcher = this._watcher;
		const scrollPos = watcher.getScrollPos();

		// recycle after _fit beacause prepend and append are occured simultaneously by scroll.
		if (!isLayout && useRecycle && !this._isLoading()) {
			this._infinite.recycle(scrollPos, isAppend);
		}

		const size = this._getEdgeValue("end");

		if (isAppend) {
			this._setContainerSize(size + this._status.loadingSize || 0);
			if (scrollPos > 0) {
				!IS_IOS && this._scrollTo(scrollPos);
			}
		}
		!isLayout && this._process(PROCESSING, false);

		/**
		 * This event is fired when layout is successfully arranged through a call to the append(), prepend(), or layout() method.
		 * @ko      . append()  prepend() , layout()        
		 * @event eg.InfiniteGrid#layoutComplete
		 *
		 * @param {Object} param The object of data to be sent to an event <ko>   </ko>
		 * @param {Array} param.target Rearranged card elements<ko>  </ko>
		 * @param {Boolean} param.fromCache Check whether these items are cache or not <ko>    .</ko>
		 * @param {Boolean} param.isLayout Returns true if this is an event called by resize event or layout method. Returns false if this is an event called by adding an item. <ko>     layout()    true,    false .</ko>
		 * @param {Boolean} param.isAppend Checks whether the append() method is used to add a card element. It returns true even though the layoutComplete event is fired after the layout() method is called. <ko>  append()   . layout()    layoutComplete   'true' .</ko>
		 * @param {Boolean} param.isScroll Checks whether scrolling has occurred after the append(), prepend(), ..., etc method is called <ko>append, prend      .</ko>
		 * @param {Number} param.scrollPos Current scroll position value relative to the infiniteGrid container element. <ko>infiniteGrid      </ko>
		 * @param {Number} param.orgScrollPos Current position of the scroll <ko>  </ko>
		 * @param {Number} param.size The size of container element <ko>  </ko>
		 * @param {Boolean} param.isTrusted Returns true if an event was generated by the user action, or false if it was caused by a script or API call <ko>     true,  API    false .</ko>
		 * @param {Function} param.endLoading End loading after startLoading() for append/prepend loading data. <ko>   append/prepend startLoading()    .</ko>
		 * @param {Object} param.endLoading.userStyle The custom style to apply to this loading bar for start. <ko>         </ko>
		 */
		this.trigger("layoutComplete", {
			target: items.concat(),
			isAppend,
			isTrusted,
			fromCache,
			isLayout,
			isScroll: viewSize < watcher.getContainerOffset() + size,
			scrollPos,
			orgScrollPos: watcher.getOrgScrollPos(),
			size,
			endLoading: (userStyle: StyleType) => {
				this.endLoading(userStyle);
			},
		});
		this._infinite.scroll(scrollPos);
	}
	private _reset() {
		this._status = {
			processingStatus: IDLE,
			loadingSize: 0,
			loadingStyle: {},
		};
	}
}

export default InfiniteGrid;
